import {img} from '../utils/images'

const styles = {
    infoSection: {
        padding: 40,
        minHeight: '100vh',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center'
    }
}

const HomePageInfo = () => {
    return (
        <div style={styles.infoSection}>
            <img src={img.yfny2018.url} alt={img.yfny2018.alt}/>
        </div>
    )
}

export default HomePageInfo;



